/* Estilo geral */
body {
  background-color: #D32F2F; /* Vermelho natalino */
  font-family: 'Courier New', Courier, monospace;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-align: center;
  overflow-x: hidden;
}

/* Título com tema natalino */
.title {
  font-size: 3rem;
  color: #FFD700; /* Dourado, típico do Natal */
  text-shadow: 2px 2px 5px rgba(255, 255, 255, 0.7);
  margin-bottom: 20px;
  animation: glow 1.5s ease-in-out infinite alternate;
}

/* Efeito de brilho no título */
@keyframes glow {
  0% { text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #FFD700, 0 0 20px #FFD700, 0 0 25px #FFD700; }
  100% { text-shadow: 0 0 10px #fff, 0 0 20px #FFD700, 0 0 30px #FFD700, 0 0 40px #FFD700, 0 0 50px #FFD700; }
}

/* Countdown de dias */
.countdown p {
  font-size: 1.5rem;
  margin-top: 20px;
  color: #fff;
}

/* Caixa de mensagens */
.input-container textarea {
  width: 80%;
  padding: 10px;
  margin-top: 20px;
  border-radius: 10px;
  border: 2px solid #fff;
  font-size: 1.2rem;
  background-color: rgba(255, 255, 255, 0.8);
  color: #333;
  resize: vertical;
}

input[type="color"], input[type="file"] {
  margin-top: 10px;
  border: none;
  padding: 10px;
  border-radius: 5px;
}

button {
  background-color: #FFD700;
  color: #D32F2F;
  border: none;
  padding: 12px 24px;
  font-size: 1.2rem;
  border-radius: 8px;
  margin-top: 10px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
}

button:hover {
  transform: scale(1.05);
}

/* Lista de mensagens */
.messages-list {
  margin-top: 30px;
}

.messages-list ul {
  list-style: none;
  padding: 0;
}

.messages-list li {
  font-size: 1.2rem;
  background-color: rgba(255, 255, 255, 0.7);
  margin: 10px 0;
  padding: 10px;
  border-radius: 8px;
}

/* Seção de configuração de plano de fundo */
.background-settings {
  margin-top: 30px;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
}

/* Animação de flocos de neve */
@keyframes snowflakes {
  0% { transform: translateY(-100px); }
  100% { transform: translateY(100vh); }
}

.snowflake {
  position: fixed;
  top: -50px;
  left: calc(100% * Math.random());
  font-size: 30px;
  color: #fff;
  animation: snowflakes 10s linear infinite;
  animation-delay: calc(1s * Math.random());
}
